<script lang="ts">
  import {
    defineComponent,
    onMounted, onBeforeMount, onUpdated, ref
  } from 'vue';
  import { useRoute, useRouter } from 'vue-router';
  import { useSSR } from '@/use/useSSR';
  import Loader from '@/components/Loader/index.vue';

  import img from './assets/img/blog/elem_01.png'
  import img2 from './assets/img/blog/elem_02.jpg'
  import thumbUp from '@/assets/icons/thumbs-up.svg';
  import thumbDown from '@/assets/icons/thumbs-down.svg';

  import axios from 'axios'
  import * as qs from 'qs'

  export default defineComponent({
    name: 'BlogPost',
    components: { Loader },
    setup() {
      const route = useRoute()
      const router = useRouter()
      const dataBlog = ref([])
      const loader = ref()
      const articleId = ref()

      const onChangePrev = async () => {
        const articleId = dataBlog.value?.id
        console.log(articleId)
        try {
          await axios.get('http://localhost:1337/api/blogs/' + articleId + '/prev?populate=%2A')
            .then(response => {
              console.log(response)
              const res = response.data.data[0].attributes.slug

              router.push(
                {
                  name: 'BlogPost',
                  path: '/blog/investitsii/',
                  params: { id: res, locale: 'ru' },
                  replace: true
                }
              )
              dataBlog.value = response?.data?.data[0]

            })
        } catch (e) {
          console.error(e)
        } finally {
        }
      }
      const onChangeNext = async () => {
        const articleId = dataBlog.value?.id
        console.log(articleId)
        try {
          await axios.get('http://localhost:1337/api/blogs/' + articleId + '/next?populate=%2A')
            .then(response => {
              console.log(response)
              const res = response.data.data[0].attributes.slug
              console.log(res)
              router.push(
                {
                  name: 'BlogPost',
                  path: '/blog/investitsii/',
                  params: { id: res, locale: 'ru' },
                  replace: true
                }
              )
              dataBlog.value = response?.data?.data[0]

            })
        } catch (e) {
          console.error(e)
        } finally {
        }
      }

      onBeforeMount(async () => {
        loader.value = true
        try {
          await axios.get('http://localhost:1337/api/blogs/' + route.params.id + '?populate=%2A')
            .then(response => {
              dataBlog.value = response?.data?.data
            })
        } catch (e) {
          console.error(e)
        } finally {
          loader.value = false
        }

      })

      onUpdated(() => {
        console.log('onUpdated')
        console.log(dataBlog.value)
      })

      return {
        img,
        img2,
        dataBlog,
        loader,

        onChangePrev,
        onChangeNext,

        thumbUp,
        thumbDown,
      };
    },
  });
</script>

