.picker {
  position: relative;
}

.picker__active {
  position: relative;
  z-index: 5;
  white-space: nowrap;
  font-size: 16px;
  cursor: pointer;
  color: #FEC006;
}

.picker__active .icon {
  top: -2px;
  margin-left: 5px;
  font-size: 8px;
  transition: .3s;
}

.picker__list {
  position: absolute;
  top: 100%;
  left: -20px;
  z-index: 10;
  opacity: 0;
  visibility: hidden;
  margin: 0;
  padding: 0 20px 15px;
  list-style: none;
  transition: .3s;
}

.picker__list li {
  margin-bottom: 5px;
  padding: 0;
  font-size: 16px;
  font-weight: 400;
}

.picker__list li:last-child {
  margin: 0;
}

