  text-decoration: none;
  color: #FEC006;
}

.picker__list a:hover {
  color: #2A8634;
}

.picker:hover .picker__active .icon {
  transform: rotate(180deg);
}

.picker:hover .picker__list {
  position: absolute;
  opacity: 1;
  visibility: visible;
  padding-top: 5px;
}

