.modal {
  display: none;
  align-items: flex-start;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  overflow: auto;
  opacity: 0;
  padding: 50px 40px;
  width: 100vw;
  height: 100vh;
  transition: opacity .3s;
}

.modal .before {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
  background: #000000;
  opacity: .5;
  width: 100vw;
  height: 100vh;
}

.modal__inner {
  position: relative;
  z-index: 10;
  background: #FFFFFF;
  margin: auto;
  width: 1000px;
  max-width: 95%;
}

.modal__close {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -40px;
  right: 0;
  z-index: 10;
  border: none;
  background: transparent;
  width: 40px;
  height: 40px;
  font-size: 12px;
  color: #FFFFFF;
  cursor: pointer;
  transform: translateX(40px);
  transition: .3s;
}

.modal__close:hover {
  color: #FEC006;
}

.modal__closeOuter {
  height: 0;
}

.modal__video {
  position: relative;
  overflow: hidden;
  font-size: 0;
  width: 100%;
  max-height: 100vh;
}

.modal__video iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal__image {
  overflow: hidden;
  font-size: 0;
}

.modal__image img {
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 100%;
  max-height: 50vh;
}

.modal__body {
  padding: 30px 50px;
  color: #797979;
}

.modal__body .more {
  margin-top: 40px;
}

.modal__title {
  border-bottom: 1px solid #E2E2E2;
  margin-top: 0;
  padding-bottom: 20px;
  font-size: 24px;
  font-weight: 500;
  color: #000000;
}

.modal.opened {
  display: flex;
}

@media (max-width: 479px) {
  .modal {
    padding: 25px 15px 70px;
  }

  .modal__close {
    top: -25px;
    right: 15px;
  }

  .modal__body {
    padding: 20px 15px;
  }
}

.postsList {
