.sliderNav button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: 0;
  background: transparent;
  padding: 0;
  width: 30px;
  height: 30px;
  color: #2A8634;
  cursor: pointer;
}

.sliderNav button span {
  position: relative;
  background: #2A8634;
  width: 100%;
  height: 2px;
  max-width: 15px;
  transition: .3s;
}

.sliderNav button span::before,
.sliderNav button span::after {
  content: '';
  display: inline-block;
  position: absolute;
  border-radius: inherit;
  background: inherit;
  width: 10px;
  height: 2px;
}

.sliderNav button span::before {
  top: -3px;
}

.sliderNav button span::after {
  bottom: -3px;
}

.sliderNav button:hover span {
  background: #FEC006;
  max-width: 25px;
}

.sliderNav button.arr-left {
  justify-content: flex-start;
  padding-left: 6px;
}

.sliderNav button.arr-left span {
  border-radius: 1px 0 0 1px;
}

.sliderNav button.arr-left span::before,
.sliderNav button.arr-left span::after {
  left: -1px;
}

.sliderNav button.arr-left span::before {
  transform: rotate(-45deg);
}

.sliderNav button.arr-left span::after {
  transform: rotate(45deg);
}

.sliderNav button.arr-right {
  justify-content: flex-end;
  padding-right: 6px;
}

.sliderNav button.arr-right span {
  border-radius: 0 1px 1px 0;
}

.sliderNav button.arr-right span::before,
.sliderNav button.arr-right span::after {
  right: -1px;
}

.sliderNav button.arr-right span::before {
  transform: rotate(45deg);
}

.sliderNav button.arr-right span::after {
  transform: rotate(-45deg);
}

