.videosList {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  width: calc(100% + 30px);
}

.videosList .video {
  width: 25%;
}

.video {
  position: relative;
  padding: 15px;
}

.video::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  background: #FFFFFF;
  opacity: 0;
  width: 100%;
  height: 100%;
  transition: .3s;
}

.video__inner {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 10;
  min-height: 100%;
}

.video__img {
  position: relative;
  margin-bottom: 27px;
  padding-top: 56%;
  height: 0;
}

.video__img img {
  position: absolute;
  top: 0;
  left: 0;
  -o-object-fit: cover;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.video__name {
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  flex: 0 0 auto;
  margin: 0;
  width: 100%;
  font-size: 20px;
  font-weight: 500;
  transform-origin: 0 50%;
  transition: .3s;
}

.video__info {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 5;
  width: 100%;
  transition: .3s .1s;
}

.video__buttons {
  position: relative;
  top: 20px;
  z-index: 10;
  background: #FFFFFF;
  opacity: 0;
  visibility: hidden;
  margin-top: auto;
  transition: .3s;
}

.video__date {
  white-space: nowrap;
  font-size: 16px;
  color: #797979;
}

.video .separator {
  background: #E2E2E2;
  margin: 20px 0;
  width: 100%;
  height: 1px;
}

.video__params {
  display: flex;
  align-items: baseline;
  font-size: 12px;
}

.video__params .price {
  white-space: nowrap;
  color: #2A8634;
}

.video__params .price.free {
  color: #FF0000;
}

.video__params .price.purchased {
  color: #2A8634 !important;
}

.video__params .difficulty {
  white-space: nowrap;
}

.video__params .divider {
  margin: 0 10px;
}

.video__paramsTop {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
  margin: 0;
}

.video__paramsTop .element {
  display: inline-flex;
  align-items: center;
  margin: 3px 45px 3px 0;
  white-space: nowrap;
  font-size: 16px;
  color: #797979;
}

.video__paramsTop .element:last-child {
  margin-right: 0;
}

.video__paramsTop .element .icon {
  align-self: flex-start;
  margin-right: 15px;
  font-size: 20px;
  color: #2A8634;
}

.video__play {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #E2E2E2;
  border-bottom: 1px solid #E2E2E2;
  height: 75px;
}

.video__open {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  height: 55px;
}

.video__openBtn {
  font-size: 16px;
  font-weight: 600;
  text-decoration: none;
  text-transform: uppercase;
  color: #000000;
}

.video__openBtn:hover {
  color: orange;
}

.video:hover::before {
  opacity: 1;
}

.video:hover .video__name {
  flex: 1 1 auto;
  transform: scale(0.8) translateY(-15px);
}

.video:hover .video__info {
  bottom: -20px;
  opacity: 0;
  visibility: hidden;
  transition: .3s;
}

.video:hover .video__buttons {
  top: 0;
  opacity: 1;
  visibility: visible;
  transition: .3s .1s;
}

