  padding: 0;
}

.filterElement {
  display: inline-block;
  position: relative;
  margin: 0 20px 15px 0;
  padding: 0;
  width: auto;
}

.filterElement::before {
  display: none;
}

.filterElement__btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 5;
  border: none;
  border-radius: 25px;
