.hamburger {
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  border: none;
  background: transparent;
  padding: 0;
  width: 35px;
  height: 35px;
  cursor: pointer;
}

.hamburger span,
.hamburger::before,
.hamburger::after {
  height: 2px;
  transition: .3s, background .3s;
}

.hamburger span {
  background: #FEC006;
  margin: 7px 0;
  width: 24px;
}

.hamburger::before,
.hamburger::after {
  content: '';
  width: 100%;
}

.hamburger::before {
  background: #FEC006;
}

.hamburger::after {
  background: #FEC006;
}

.hamburger:hover span,
.hamburger:hover::before,
.hamburger:hover::after {
  background: #2A8634;
}

.hamburger:hover span {
  width: 100%;
}

.hamburger:active span,
.hamburger:active::before,
.hamburger:active::after {
  background: #2A8634;
}

