.pagination {
  display: flex;
  align-items: center;
}

.pagination li {
  justify-content: center;
  margin: 0;
  padding: 0;
  width: 30px;
  font-size: 13px;
  text-align: center;
  color: #797979;
}

.pagination li::before {
  display: none;
}

.pagination li.active a {
  border-color: #797979;
  color: #797979;
  cursor: default;
}

.pagination li.old {
  color: #c9c9c9;
}

.pagination a {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  text-decoration: none;
  color: inherit;
}

.pagination a:hover {
  color: #FEC006;
}

.pagination a.icon {
  border-color: transparent;
}

.pagination a.icon span {
  position: relative;
  background: #2A8634;
  width: 100%;
  height: 2px;
  max-width: 15px;
  transition: .3s;
}

.pagination a.icon span::before,
.pagination a.icon span::after {
  content: '';
  display: inline-block;
  position: absolute;
  border-radius: inherit;
  background: inherit;
  width: 10px;
  height: 2px;
}

.pagination a.icon span::before {
  top: -3px;
}

.pagination a.icon span::after {
  bottom: -3px;
}

.pagination a.icon:hover span {
  background: #FEC006;
  max-width: 25px;
}

.pagination a.icon.arr-left {
  justify-content: flex-start;
  padding-left: 6px;
}

.pagination a.icon.arr-left span {
  border-radius: 1px 0 0 1px;
}

.pagination a.icon.arr-left span::before,
.pagination a.icon.arr-left span::after {
  left: -1px;
}

.pagination a.icon.arr-left span::before {
  transform: rotate(-45deg);
}

.pagination a.icon.arr-left span::after {
  transform: rotate(45deg);
}

.pagination a.icon.arr-right {
  justify-content: flex-end;
  padding-right: 6px;
}

.pagination a.icon.arr-right span {
  border-radius: 0 1px 1px 0;
}

.pagination a.icon.arr-right span::before,
.pagination a.icon.arr-right span::after {
  right: -1px;
}

.pagination a.icon.arr-right span::before {
  transform: rotate(45deg);
}

.pagination a.icon.arr-right span::after {
  transform: rotate(-45deg);
}

.paginationOuter {
  display: flex;
  align-items: center;
  margin-top: 30px;
}

.paginationOuter > .more {
  margin-right: auto;
}

.paginationOuter .pagination {
  margin-left: 20px;
}

@media (max-width: 567px) {
  .paginationOuter {
